body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Login page style*/
.App {
  text-align: center;
  color: #282c34;
  background-color: #282c34;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #003C60;
}
.loginButton{
  position: absolute;
  width: 400px;
  height: 600px;
  text-align: center;
  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, -25%);
          transform: translate(-50%, -25%);
  padding: 50px;
  border-radius: 25px;
  border: 2px solid #003C60;
}
.loginButton p{
  font-family: "Helvetica Neue";
  font-size: x-large;


}
.Exam{
  padding: 50px;
}

.button{
  -webkit-justify-content: center;
          justify-content: center;
}



/* Home page style*/
.Home{
    min-width: 630px;
    overflow: scroll;
    position: absolute;
}


.topBar{
  position: fixed;
  -webkit-align-content: center;
          align-content: center;
  top: 0;
  margin-left: 0%;
  margin-right: 0%;
  height: 70px;
  width: 100%;
  background-color: #f2f2f2;
}

.right{
  float: right;
  padding: 10px;
  font-size: x-large;
  color: #003C60 ;
}
.leftA{
  float: left;
  padding-left: 10px;
  color: #003C60 ;
  font-family: "Helvetica Neue";
}
.leftB{
  float: left;
  color: #00CCBC;
  font-family: "Helvetica Neue";
}
.welcome{
  padding-top: 100px;
  padding-left: 50px;
  padding-bottom: 100px;

  font-family: "Helvetica Neue";
  color: #003C60 ;
}



.buttons{
  position: absolute;
  top: 225px;
  right: 25%;
}
.buttons button{
  display: inline-block;
  position: relative;
  float: right;
  width: 50%;
  height: 50px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #60f3ab ;
  color:  #003C60;
  border-radius: 5px;

}
.buttons select{
  display: inline-block;
  position: relative;
  float: right;
  height: 50px;
    width: 50%;





}
.alert{
    display: inline-block;
    float: right;

}



.logout{
  float: right;
  padding-top: 25px;
}

.modal h1{
  text-align: center;
  font-family: "Helvetica Neue";
  padding-bottom: 1%;
  color: #003C60;
}
.modal label{
  -webkit-justify-content: center;
          justify-content: center;
  font-family: "Helvetica Neue";
  color: #003C60;
}
.modal button{
  float: right;
  width: 25%;
}

.id{
  padding-bottom: 1%;
}
.scanCode {
  position: relative;
  text-align: center;
  font-weight: bold;
  font-size: large;
}

.exam{
    position: relative;
    -webkit-align-content: center;
            align-content: center;
    margin-left: 25%;
    margin-right: 25%;
    top: 25%;
    margin-top: 20px;
    padding: 50px;
    border: 2px solid black;
    border-radius: 25px;

}
.exam p{
    position: relative;
    display: inline-block;
    float: left;
    font-size: 14pt;
    color: #0081A1;


}
.exam p:hover{
    color: #035B8F;
}
.exam button:hover{
    background-color: white;
    color: #003C60;
}
.exam button {
    display: inline-block;
    position: relative;
    float: right;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #60f3ab;
    color: #003C60;
    border-radius: 10px;
    height: 50px;
    margin-bottom: 25px;
    width: 20%;
}

.tab-list {
    border-bottom: 1px solid #ccc;
}

.tab-list-item {
    display: inline-block;
    list-style: none;
    margin-bottom: -1px;
    padding: 0.5rem 0.75rem;
    -webkit-align-self: center;
            align-self: center;
}

.tab-list-active {
    background-color: rgb(255, 255, 255);
    border: solid #ccc;
    border-width: 1px 1px 0 1px;
}

.keyBox label {
  height:40px;
  width: 40px;
  background-color: #e7e7e7;
  border: 2px solid #555555;
  color: black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 10px 5px;
  cursor: pointer;
}

.keyBox input[type=checkbox]:checked + label {
    background: #000000;
    color: white;
}
.keyBox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.items {
	overflow: hidden;
  margin-left: 10%;
  margin-right:10%;
}
.items .item {
	float: left;
	width: 25%;
  box-sizing: border-box;
  padding: 10px;
  position: relative;
}
.items .itemEmpty {
	float: left;
	width: 25%;
  box-sizing: border-box;
  padding: 10px;
  background: #ffb3b3;
  position: relative;
}
.scrollable {
    width: auto;
    height: 900px;
    overflow: scroll
}
.answerKeyTitle{
    padding-top: 60px;
    margin: 20px;
    color: #003C60;
    font-family: "Helvetica Neue";
}
.examName{
    margin: 20px;
    color: #003C60;
    font-family: "Helvetica Neue";
}
.float{
	position:fixed;
	width:120px;
  height: 50px;
	bottom:20px;
	right:40px;
  background-color: #60f3ab;
  color: #003C60;
  border-radius: 10px;
	text-align:center;
}
.questionId{
  font-weight: bold;
  padding-right: 2px;
}
.questionId:after{
  font-weight: bold;
  content: "."
}
.div{
  background-color: red;
}

